@use 'partials/globals' as *;
@use 'partials/mixins' as *;
@use 'partials/responsive' as *; 
@use 'partials/typography' as *; 
@use 'partials/variables' as *;

#wrapper {
    display: inherit;
    flex-direction: column;
    align-items: center;
    box-shadow: 0 10px 20px rgba(17, 17, 17, 0.161);
    padding: 30px 50px;
    border-radius: 5px;
    #error-message {
        color: red;
    }
    h1 {
        color: #243948;
    }
    .form-submit {
        padding: 25px 0;
    }
    .form-element {
        @include flex-central-align;
        transition:                 $transition-03;
        -webkit-transition:         $transition-03;
        -moz-transition:            $transition-03;
        -o-transition:              $transition-03;
        button:only-child {
            padding: 10px 25px;
            border-radius: 5px;
            border: none;
            box-shadow: 0 10px 20px rgba(17, 17, 17, 0.161);
            background: white;
            font-size: 16px;
            width: 260px;
            cursor: pointer;
            transition:             $transition-03;
            -webkit-transition:     $transition-03;
            -moz-transition:        $transition-03;
            -o-transition:          $transition-03;
            &:hover {
                background:         $secondary-color;
                transition:         $transition-03;
                -webkit-transition: $transition-03;
                -moz-transition:    $transition-03;
                -o-transition:      $transition-03;
            }
        }
        input {
            height: 25px;
            padding: 5px;
            border-radius: 5px;
            border: 1px solid cornflowerblue;
            width: 250px;
            font-size: 18px;
            caret-color: #5fa0be;
            color: #344147;
            transition:             $transition-03;
            -webkit-transition:     $transition-03;
            -moz-transition:        $transition-03;
            -o-transition:          $transition-03;
            &:focus {
                transition:         $transition-03;
                -webkit-transition: $transition-03;
                -moz-transition:    $transition-03;
                -o-transition:      $transition-03;
                background: $secondary-color;
            }
            &:hover {
                border-color: #243948;
                transition:         $transition-03;
                -webkit-transition: $transition-03;
                -moz-transition:    $transition-03;
                -o-transition:      $transition-03;
            }
        }
        label {
            padding: 10px 0;
            font-size: 18px;
            color: #243948;
        }
    }
}